
import { computed, defineComponent, toRefs } from 'vue';

export default defineComponent({
  name: 'AppIcon',
  props: {
    iconName: {
      type: String,
      required: true
    },
    iconColor: {
      type: String,
      required: false,
      default: 'text-gray-600'
    },
    height: {
      type: String,
      required: false,
      default: 'h-6'
    },
    width: {
      type: String,
      required: false,
      default: 'h-6'
    }
  },
  setup(props) {
    const iconFullName = computed(() => props.iconName + 'Icon');
    return {
      iconFullName,
      ...toRefs(props)
    };
  }
});
