
import { defineComponent, toRefs } from 'vue';
import AppIcon from '@/components/icons/AppIcon.vue';
import { Routes } from '@/types/enums';
import { useCurrentRoute } from '@/hooks/useCurrentRoute';

export default defineComponent({
  name: 'NavigationItem',
  components: {
    AppIcon
  },
  emits: ['update:close-mobile-sidebar'],
  props: {
    route: {
      type: String,
      required: true
    },
    iconName: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: false,
      default: ''
    },
    onlyIcon: {
      type: Boolean,
      required: false,
      default: true
    },
    mobileSidebarOpen: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  setup(props) {
    const { isRoute } = useCurrentRoute(props.route);

    return {
      isRoute,
      routes: Routes,
      ...toRefs(props)
    };
  }
});
